<template>
  <div>
    <!-- loading  -->
    <loading :active.sync="isLoading" :is-full-page="true"></loading>

    <div class="mb-4 text-right">
      <button type="button" class="btn btn-dark" @click.prevent="openModal('create')">
        
      </button>
    </div>

    <div class="table-responsive">
      <table class="table table-striped">
        <thead>
          <tr>
            <th scope="col" class="d-none d-md-table-cell"></th>
            <th scope="col" class="d-none d-lg-table-cell"></th>
            <th scope="col"></th>
            <th scope="col" class="d-none d-lg-table-cell"></th>
            <th scope="col" class="d-none d-sm-table-cell"></th>
            <th scope="col" class="d-none d-sm-table-cell"></th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody v-if="products.length">
          <tr v-for="item in products" :key="item.id">
            <td class="d-none d-md-table-cell align-middle">{{ item.category }}</td>
            <td class="d-none d-lg-table-cell">
              <span class="thumbnail"
               :style="{backgroundImage: 'url(' + item.imageUrl[0] + ')'}"></span>
            </td>
            <td class="align-middle">{{ item.title }}</td>
            <td class="d-none d-lg-table-cell align-middle text-right">
              {{ item.origin_price | currency }}
            </td>
            <td class="d-none d-sm-table-cell align-middle text-right">
              {{ item.price | currency }}
            </td>
            <td class="d-none d-sm-table-cell align-middle">
              <span class="text-success" v-if="item.enabled"></span>
              <span class="text-muted" v-else></span>
            </td>
            <td class="align-middle">
              <div class="btn-group btn-group-sm">
                <button type="button" class="btn btn-outline-dark"
                 @click.prevent="openModal('update', item)">
                  
                </button>
                <button type="button" class="btn btn-outline-danger"
                 @click.prevent="openModal('del', item)">
                  
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- editModal -->
    <div class="modal fade" id="editModal" role="dialog" aria-labelledby="editModalTitle"
     aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered modal-xl" role="document">
        <div class="modal-content">
          <div class="modal-header bg-dark text-light">
            <h5 class="modal-title" id="editModalTitle">
              {{ status === 'create' ? '' : '' }}
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="container-fluid">
              <div class="row">
                <div class="col-md-4">
                  <div class="form-group" v-for="i in 5" :key="i">
                    <label :for="'imageUrl' + i"></label>
                    <input type="text" :id="'imageUrl' + i" class="form-control"
                     placeholder="" v-model="tempProduct.imageUrl[i-1]" required>
                  </div>
                  <div class="form-group">
                    <p class="mb-2">( 2 MB)</p>
                    <div class="custom-file">
                      <input type="file" class="custom-file-input" id="file" accept="image/*"
                       ref="file" @change="uploadFile">
                      <label class="custom-file-label" for="file"></label>
                    </div>
                  </div>
                </div>
                <div class="col-md-8">
                  <div class="form-group">
                    <label for="title"></label>
                    <input type="title" id="title" class="form-control" placeholder=""
                     v-model="tempProduct.title" required>
                  </div>
                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="category"></label>
                      <input type="text" id="category" class="form-control" placeholder=""
                       v-model="tempProduct.category" required>
                    </div>
                    <div class="form-group col-md-6">
                      <label for="unit"></label>
                      <input type="text" id="unit" class="form-control" placeholder=""
                       v-model="tempProduct.unit" required>
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="origin_price"></label>
                      <input type="number" id="origin_price" class="form-control"
                       placeholder="" v-model="tempProduct.origin_price" required>
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price"></label>
                      <input type="number" id="price" class="form-control" placeholder=""
                       v-model="tempProduct.price" required>
                    </div>
                  </div>
                  <hr>
                  <div class="form-group">
                    <label for="description"></label>
                    <textarea class="form-control" id="description" rows="3" placeholder=""
                     v-model="tempProduct.description" require></textarea>
                  </div>
                  <div class="form-group">
                    <label for="content"></label>
                    <vue-editor id="content" v-model="tempProduct.content" placeholder=""/>
                  </div>
                  <div class="form-group form-check">
                    <input type="checkbox" class="form-check-input" id="enabled"
                    v-model="tempProduct.enabled" :true-value="true" :false-value="false">
                    <label class="form-check-label" for="enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
            <button type="submit" class="btn btn-dark" @click.prevent="updateProduct()">
              
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- delModal -->
    <div class="modal fade" id="delModal" role="dialog" aria-labelledby="delModalTitle"
     aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header bg-danger text-light">
            <h5 class="modal-title" id="delModalTitle"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">
              
            </button>
            <button type="button" class="btn btn-outline-danger"
             @click.prevent="delProduct()">
              
            </button>
          </div>
        </div>
      </div>
    </div>

    <Pagination :pages="pagination" @get-data="getProducts"/>
  </div>
</template>

<script>
/* global $ */
import { VueEditor } from 'vue2-editor';
import Pagination from '@/components/Pagination.vue';

export default {
  name: 'ProductsManage',
  props: ['token'],
  data() {
    return {
      isLoading: false,
      pagination: {},
      products: {},
      tempProduct: {
        imageUrl: [],
      },
      status: '',
    };
  },
  methods: {
    getProducts(page = 1) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/products?page=${page}&paged=10`;
      vm.isLoading = true;
      vm.$http.get(url).then((res) => {
        vm.products = res.data.data;
        vm.pagination = res.data.meta.pagination;
        vm.isLoading = false;
      });
    },
    getProduct(id) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product/${id}`;
      vm.isLoading = true;
      vm.$http.get(url).then((res) => {
        this.tempProduct = res.data.data;
        $('#editModal').modal('show');
        vm.isLoading = false;
      }).catch(() => {
        $('#editModal').modal('hide');
        vm.isLoading = false;
        vm.$swal({
          icon: 'error',
          title: '',
        });
      });
    },
    openModal(status, item) {
      this.status = '';
      switch (status) {
        case 'del':
          $('#delModal').modal('show');
          this.tempProduct = { ...item };
          break;
        case 'update':
          this.getProduct(item.id);
          this.status = 'update';
          break;
        case 'create':
          $('#editModal').modal('show');
          this.tempProduct = {
            imageUrl: [],
          };
          this.status = 'create';
          break;
        default:
          break;
      }
    },
    updateProduct() {
      const vm = this;
      let status = '';
      let method = 'post';
      let url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product`;
      if (vm.status === 'update') {
        method = 'patch';
        url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product/${vm.tempProduct.id}`;
        status = '';
      }
      vm.isLoading = true;
      vm.$http[method](url, this.tempProduct).then(() => {
        vm.getProducts();
        $('#editModal').modal('hide');
        vm.$swal({
          icon: 'success',
          title: `${status}`,
        });
      }).catch(() => {
        $('#editModal').modal('hide');
        vm.isLoading = false;
        vm.$swal({
          icon: 'error',
          title: `${status}`,
        });
      });
    },
    delProduct() {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product/${vm.tempProduct.id}`;
      vm.isLoading = true;
      vm.$http.delete(url).then(() => {
        vm.getProducts();
        $('#delModal').modal('hide');
        vm.$swal({
          icon: 'success',
          title: '',
        });
      }).catch(() => {
        vm.isLoading = false;
        vm.$swal({
          icon: 'error',
          title: '',
        });
      });
      $('#delModal').modal('hide');
    },
    uploadFile() {
      const vm = this;
      const file = vm.$refs.file.files[0];
      const formData = new FormData();
      formData.append('file', file);
      vm.isLoading = true;
      const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/storage`;
      this.$http.post(url, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      }).then((res) => {
        if (res.status === 200) {
          // 
          const array = vm.tempProduct.imageUrl.filter((item) => item !== null);
          array.push(res.data.data.path);
          vm.tempProduct.imageUrl = array;
        }
        vm.isLoading = false;
        vm.$swal({
          icon: 'success',
          title: '',
        });
      }).catch(() => {
        vm.isLoading = false;
        vm.$swal({
          icon: 'error',
          title: '',
        });
      });
    },
  },
  components: {
    VueEditor,
    Pagination,
  },
  created() {
    this.getProducts();
  },
};
</script>

<style lang="scss" scoped>
@import '@/assets/all';

.thumbnail {
  display: inline-block;
  width: 80px;
  height: 80px;
  margin: 0 auto;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
</style>
